import React, { Component } from "react";

import "./Chat.css";
import UserIcon from "../../assets/userIcon.png";
import ArrowIcon from "../../assets/arrowIcon.png";
import dp from "../../assets/dp.jpg";

import ChatList from "../../components/chatList/ChatList";

export default class Chat extends Component {
  state = {
    showList: true,
  };
  render() {
    const { showList } = this.state;

    return (
      <div className="chatBG">
        <div className="container pt-5">
          <div className="row px-3 p-md-0">
            <button
              className="d-block d-md-none btn btn-lg btn-light btn-block mb-3"
              onClick={() => this.setState({ showList: !showList })}
            >
              Toggle List
            </button>

            {showList ? (
              <div className="col-md-3 col-12 mb-5 mb-md-0 ml-md-5 bg-white rounded shadow p-3">
                <div className="myOverFlow">
                  <img alt="dp" height="25" src={UserIcon} />
                  <span className="ml-3">75 CONTACTS</span>
                  <hr />
                  <ChatList />
                  <ChatList />
                  <ChatList />
                  <ChatList />
                  <ChatList />
                  <ChatList />
                  <ChatList />
                  <ChatList />
                  <ChatList />
                  <ChatList />
                  <ChatList />
                </div>
                <div className="pt-3">
                  <input
                    type="text"
                    className="form-control s"
                    placeholder="Search"
                  />
                </div>
              </div>
            ) : null}
            <div className="col-md-7 col-12 mb-5 mb-md-0 ml-md-5 bg-white rounded shadow p-3 pt-4">
              <div className="myOverFlow">
                <div className="row p-2">
                  <div className="col-3 col-md-2"></div>
                  <div className="col-9 myBGColor p-2 px-3">
                    <h6>MEMBER NAME</h6>
                    <p className="small text-muted">10-10-2020 02.30 am</p>
                    <p className="mt-n2">
                      Lorem ipsum dolor sit amet consectetur adipisicing elit.
                      Quod quia nihil dignissimos laudantium magnam deleniti
                      odit.
                    </p>
                  </div>
                </div>

                <div className="row p-2">
                  <div className="col-3 col-md-2"></div>
                  <div className="col-9">
                    <img
                      alt="dp"
                      width="75px"
                      src={dp}
                      className="rounded mr-3"
                    />
                    <img
                      alt="dp"
                      width="75px"
                      src={dp}
                      className="rounded mr-3"
                    />
                    <img
                      alt="dp"
                      width="75px"
                      src={dp}
                      className="rounded mr-3"
                    />
                    <img
                      alt="dp"
                      width="75px"
                      src={dp}
                      className="rounded mr-3"
                    />
                  </div>
                </div>

                <div className="row p-2">
                  <div className="col-3 col-md-2 d-flex align-self-center ">
                    <img
                      alt="dp"
                      width="50"
                      src={dp}
                      className="rounded-circle my-auto"
                    />
                  </div>
                  <div className="col-9 speechBubble p-2 px-3">
                    <h6>MEMBER NAME</h6>
                    <p className="small text-muted">10-10-2020 02.30 am</p>
                    <p className="mt-n2">
                      Lorem ipsum dolor sit amet consectetur adipisicing elit.
                      Quod quia nihil dignissimos laudantium magnam deleniti
                      odit.
                    </p>
                  </div>
                </div>
              </div>
              <div className="row p-2">
                <div className="col-3 col-md-2 d-flex align-self-center "></div>
                <div className="col-9 p-0">
                  <div className="input-group myBGColor">
                    <input
                      type="text"
                      className="form-control"
                      placeholder="Send"
                    />
                    <div>
                      <span className="myBGColor input-group-text">
                        <img alt="dp" height="23" src={ArrowIcon} />
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
